<template>
  <div>

<!--   -->
    <v-form>
      <v-text-field
        v-model="categoryname"
        label=" "
        type="text"
      ></v-text-field>


    <v-divide></v-divide>
    <v-divide></v-divide>

<!--   -->

      <v-text-field
        v-model="options.optionname"
        label=" "
        type="text"
      ></v-text-field>
      <v-text-field
        v-model="options.optionprice"
        label=" "
        type="number"
      ></v-text-field>


    <v-divide></v-divide>
      <v-btn  type="submit" @click="addstore" >  </v-btn>
    </v-form>


  </div>
</template>

<script>
import axios from 'axios';

export default {
  // import axios from 'axios';
  props: ['items'],

  // discount 2  


  data () {
    return {
      categoryname: '',
      options:
      {
        optionname: '',
        optionprice: ''
      }

    }
  },

  methods: {
    addstore() {
      axios({
          method: 'post',
          url: '/api/setcategory',
          data: {
            categoryname: this.categoryname,
            options: this.options
           }
      })
      .then(() => {
            console.log( "categoryname:" + this.categoryname,
            "options:" + this.options )
            alert(" !");
        })
      .catch(function (error) {
          console.log(error);
          alert(" !");
    });
  }
}



  }

</script>
